import { useState } from "react"
import { Document, Page, pdfjs } from "react-pdf"
import { motion, AnimatePresence } from "framer-motion"
import DashboardLayout from "../components/layout/DashboardLayout"
import { Button } from "../components/ui/button"
import { MessageSquare, Download, Share2, ChevronLeft, ChevronRight, ZoomIn, ZoomOut, Search } from "lucide-react"

// Set up PDF.js worker
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`

const comments = [
  {
    id: 1,
    user: "John Doe",
    text: "This clause needs to be reviewed by legal team",
    timestamp: "2 hours ago",
    page: 1,
  },
  {
    id: 2,
    user: "Sarah Smith",
    text: "I suggest we modify this section to include...",
    timestamp: "1 day ago",
    page: 2,
  },
]

export default function DocumentViewer() {
  const [numPages, setNumPages] = useState(null)
  const [pageNumber, setPageNumber] = useState(1)
  const [showComments, setShowComments] = useState(true)
  const [scale, setScale] = useState(1.0)
  const [searchQuery, setSearchQuery] = useState("")

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages)
  }

  const handleZoomIn = () => {
    setScale(prev => Math.min(prev + 0.1, 2.0))
  }

  const handleZoomOut = () => {
    setScale(prev => Math.max(prev - 0.1, 0.5))
  }

  return (
    <DashboardLayout>
      <div className="flex h-[calc(100vh-4rem)] bg-gray-50">
        {/* PDF Viewer */}
        <div className="flex-1 overflow-auto p-6">
          <motion.div 
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-white rounded-xl shadow-lg p-6"
          >
            <div className="flex justify-between items-center mb-6">
              <div>
                <h1 className="text-2xl font-bold text-gray-900">Investment Agreement</h1>
                <p className="text-sm text-gray-500 mt-1">Last modified: 2 hours ago</p>
              </div>
              <div className="flex space-x-3">
                <Button variant="outline" size="sm" className="hover:bg-gray-50">
                  <Download className="h-4 w-4 mr-2" />
                  Download
                </Button>
                <Button variant="outline" size="sm" className="hover:bg-gray-50">
                  <Share2 className="h-4 w-4 mr-2" />
                  Share
                </Button>
                <Button
                  variant={showComments ? "default" : "outline"}
                  size="sm"
                  onClick={() => setShowComments(!showComments)}
                  className="hover:bg-gray-50"
                >
                  <MessageSquare className="h-4 w-4 mr-2" />
                  Comments
                </Button>
              </div>
            </div>

            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center space-x-4">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => setPageNumber(pageNumber - 1)}
                  disabled={pageNumber <= 1}
                  className="hover:bg-gray-50"
                >
                  <ChevronLeft className="h-4 w-4" />
                </Button>
                <span className="text-sm font-medium">
                  Page {pageNumber} of {numPages}
                </span>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => setPageNumber(pageNumber + 1)}
                  disabled={pageNumber >= numPages}
                  className="hover:bg-gray-50"
                >
                  <ChevronRight className="h-4 w-4" />
                </Button>
              </div>
              <div className="flex items-center space-x-4">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleZoomOut}
                  disabled={scale <= 0.5}
                  className="hover:bg-gray-50"
                >
                  <ZoomOut className="h-4 w-4" />
                </Button>
                <span className="text-sm font-medium">{Math.round(scale * 100)}%</span>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleZoomIn}
                  disabled={scale >= 2.0}
                  className="hover:bg-gray-50"
                >
                  <ZoomIn className="h-4 w-4" />
                </Button>
              </div>
            </div>

            <div className="flex justify-center bg-gray-50 rounded-lg p-4">
              <Document
                file="/sample.pdf"
                onLoadSuccess={onDocumentLoadSuccess}
                className="max-w-full"
              >
                <Page
                  pageNumber={pageNumber}
                  renderTextLayer={false}
                  renderAnnotationLayer={false}
                  scale={scale}
                  className="shadow-lg"
                />
              </Document>
            </div>
          </motion.div>
        </div>

        {/* Comments Panel */}
        <AnimatePresence>
          {showComments && (
            <motion.div
              initial={{ width: 0, opacity: 0 }}
              animate={{ width: 360, opacity: 1 }}
              exit={{ width: 0, opacity: 0 }}
              transition={{ duration: 0.3 }}
              className="border-l border-gray-200 bg-white"
            >
              <div className="h-full flex flex-col">
                <div className="p-6 border-b border-gray-200">
                  <h2 className="text-xl font-semibold text-gray-900">Comments</h2>
                  <p className="text-sm text-gray-500 mt-1">Discuss and collaborate on this document</p>
                </div>
                <div className="flex-1 overflow-y-auto p-6">
                  {comments.map((comment) => (
                    <motion.div
                      key={comment.id}
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      className="mb-4 p-4 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                    >
                      <div className="flex justify-between items-start mb-2">
                        <span className="font-medium text-gray-900">{comment.user}</span>
                        <span className="text-sm text-gray-500">
                          {comment.timestamp}
                        </span>
                      </div>
                      <p className="text-sm text-gray-700">{comment.text}</p>
                      <div className="mt-2 text-xs text-gray-500 flex items-center">
                        <span className="bg-blue-100 text-blue-800 px-2 py-1 rounded-full">
                          Page {comment.page}
                        </span>
                      </div>
                    </motion.div>
                  ))}
                </div>
                <div className="p-6 border-t border-gray-200">
                  <textarea
                    className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-colors"
                    rows={3}
                    placeholder="Add a comment..."
                  />
                  <Button className="mt-3 w-full">Post Comment</Button>
                </div>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </DashboardLayout>
  )
} 